.label {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10px;
  font-size: 24px;
  font-weight: 500;
}
.input {
  font-size: 16px;
  font-weight: 500;
  border-radius: 5px;
  border-color: transparent;
  box-shadow: inset 0px 4px 4px rgba(0, 0, 0, 0.15);
  padding: 5px;
}
.input::placeholder {
  color: blue;
  font-size: 1.2em;
  font-style: italic;
  text-align: center;
}
