.contactsTitle {
  color: #212121;
  text-align: center;
  font-size: 40px;
  font-weight: 700;
  padding: 15px;
  margin: 0;
}
.contactList {
  display: flex;
  flex-direction: column;
  /* justify-content: center; */
  /* align-items: center; */
  gap: 10px;
  list-style: none;
  margin: 0;
  padding: 10px;
}
.item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 10px;
  border-radius: 5px;
  border-color: transparent;
  box-shadow: inset 0px 4px 4px rgba(0, 0, 0, 0.15);
  padding: 5px;
  background-color: antiquewhite;
}
.value {
  font-size: 24px;
  font-weight: 500;
  margin: 0;
}

.btn {
  color: #212121;
  background-color: burlywood;
  border: transparent solid 1px;
  border-radius: 4px;
  font-family: inherit;
  font-weight: 700;
  font-size: 12px;
  line-height: 1.88;
  letter-spacing: 0.06em;
  text-align: center;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1),
    color 250ms cubic-bezier(0.4, 0, 0.2, 1),
    box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1);
  cursor: pointer;
}

.btn:hover {
  transform: scale(1.2);
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
  background-color: blueviolet;
}

.btn:focus {
  transform: scale(1.2);
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
  background-color: bisque;
}
